body {
  font-size: 100px;
}

#psyduck {
  width: 3.6em;
  height: 4em;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#psyduck > .hair-wrapper {
  width: 100px;
  height: 100px;
