  border-radius: 20%;
}

#psyduck > .hair-wrapper > .hair:first-child {
  transform: rotate(-50deg)translate(-.03em);
}

#psyduck > .hair-wrapper > .hair:last-child {
  transform: rotate(50deg)translate(.03em);
}

#psyduck > .head {
  width: 1.5em;
  height: 1.2em;
  background-color: #eecc2f;
  border-radius: 50%;
  position: absolute;
  top: 1em;
  left: 50%;
  transform: translate(-50%, -50%);
}

#psyduck > .body {
  z-index: -1;
  width: 1.8em;
  height: 1.7em;
  background-color: #eecc2f;
  border-radius: 85% / 90%;
  position: absolute;
  top: 2.1em;
  left: 50%;
  transform: translate(-50%, -50%);
}

#psyduck .eye {
  width: 100%;
  height: .5em;
  position: absolute;
}

#psyduck .eye > div {
  width: .35em;
  height: .28em;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: .3em;
}

#psyduck .eye .left {
  left: 15%;
  transform: rotate(-26deg);
}

#psyduck .eye .right {
  right: 15%;
  transform: rotate(26deg);
}

#psyduck .eye .left.dot, #psyduck .eye .right.dot {
  width: 0;
  height: 0;
  border: 2px solid #000;
  border-radius: 100%;
  position: absolute;
  top: .4em;
}

#psyduck .eye .left.dot {
  left: .35em;
}

#psyduck .eye .right.dot {
  right: .35em;
}

#psyduck .mouth {
  width: 4em;
  justify-content: center;
  display: flex;
  position: absolute;
  top: .4em;
  left: 50%;
  transform: translateX(-50%)scale(.65);
}

#psyduck .mouth .top {
  height: .5em;
  width: .5em;
  background-color: #eae8c0;
  border-radius: .25em 0 0;
  position: relative;
}

#psyduck .mouth .top:first-child {
  top: .2em;
  transform: rotate(5deg);
}

#psyduck .mouth .top:last-child {
  top: .2em;
  transform: rotate(85deg);
}

#psyduck .mouth .top:nth-child(2) {
