  transform: translateX(-50%);
}

#psyduck .stuffing {
  height: 0;
  width: .94em;
  border-bottom: 50px solid #eae8c0;
  border-left: 10px solid #0000;
  border-right: 10px solid #0000;
  margin: 0 auto;
  position: absolute;
  top: .73em;
  left: 50%;
  transform: translateX(-50%);
}

#psyduck .nostril {
  height: 0;
  width: 0;
  position: absolute;
  top: .6em;
  left: 50%;
  transform: translateX(-50%);
}

#psyduck .nostril:before, #psyduck .nostril:after {
  content: "";
  width: .05em;
  height: .1em;
  background-color: #606f6af1;
  border-radius: 50em / 100em;
  position: absolute;
}

#psyduck .nostril:before {
  left: 10px;
